import React from 'react';
import { Form, Col, Row, Container } from 'react-bootstrap';
import '../styles/homepage.css'
import Alert from 'react-bootstrap/Alert'

class Homepage extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            'data': {
                "name": "",
                "year": "",
                "major": "",
                "emailAddress": ""
            },
            apispreadsheetsUrl: "https://api.apispreadsheets.com/data/2658/",
            alertMessage: "",
            alertModal: false,
            alertType: "",
            dismissible: false
        }
    }

    resetData() {
        this.setState({
            'data': {
                "name": "",
                "year": "",
                "major": "",
                "emailAddress": ""
            }
        });
    }

    handleMessage(type, message, time, dismiss) {
        this.setState({alertModal: true, alertMessage: message, alertType: type, dismissible:dismiss});
        if (time) {
            window.setTimeout(() => {
                this.setState({alertModal: false})
            },5000);
        }
    }

    addData() {
        this.resetData();
        fetch(this.state.apispreadsheetsUrl, {
            method: "POST",
            body: JSON.stringify({"data": this.state.data}),
        }).then(res =>{
            if (res.status === 201){
                // SUCCESS
                document.getElementById("main-form").reset();
                this.handleMessage("success", "We received your info. Thanks!", true, false);
            }
            else{
                console.log(`encountered error`);
                // ERROR
                this.handleMessage("danger", "An Error occurred. Please contact us with our club email at the bottom of the webpage.", false, true);
            }
        });
    }

    addToFormData(key, value) {
        let set = this.state.data;
        set[key] = value;
        this.setState({ 'data': set });
    }

    render() {
        return (
            <div className="Homepage">
                <Container fluid>  
                    <br></br>
                    <div className="header-text" style={{paddingTop:'3%'}}>Welcome to John Quincy Adams Society at <div style={{color:'#EBC13A'}}><b> UC Berkeley</b></div></div>
                    <br></br>
                    <div className="form-text" style={{paddingTop:'2%'}}>Sign up for club info!</div>
                    <Form id="main-form">
                        <Form.Row>
                            <Col xs={{span:10, offset:1}} md={{ span: 3, offset: 3 }}>
                                <Form.Control className="form-inner" style={{marginTop:'2%'}} placeholder="Full name" onChange={(e) => {
                                    this.addToFormData("name", e.target.value);
                                }} />
                            </Col>
                            <Col xs={{span:10, offset:1}}  md={{ span: 3, offset:0 }} >
                                <Form.Control className="form-inner" style={{marginTop:'2%'}} placeholder="Class Year" onChange={(e) =>{
                                    this.addToFormData("year", e.target.value);
                                }} />
                            </Col>
                        </Form.Row>
                        <Form.Row className="second-form-row" style={{marginTop:'0%'}}>
                            <Col xs={{span:10, offset:1}}  md={{ span: 3, offset: 3 }}>
                                <Form.Control className="form-inner" style={{marginTop:'2%'}} placeholder="Major" onChange={(e) =>{
                                    this.addToFormData("major", e.target.value);
                                }} />
                            </Col>
                            <Col xs={{span:10, offset:1}}   md={{ span: 3, offset:0}}>
                                <Form.Control className="form-inner" style={{marginTop:'2%'}} placeholder="Email Address" onChange={(e) =>{
                                    this.addToFormData("emailAddress", e.target.value);
                                }} />
                            </Col>
                        </Form.Row>
                    </Form>
                    <Row>
                        <Col xs={{offset:1}}  md={{ span: 3, offset: 3 }} style={{marginTop:'2%'}}>
                            <button 
                                type="button" 
                                style={{backgroundColor: '#EBC13A'}}
                                class="btn btn-info btn-rounded form-inner"
                                disabled={this.state.data.name.length == 0 || this.state.data.year.length == 0
                                    || this.state.data.major.length == 0
                                    || this.state.data.emailAddress.length == 0}
                                    onClick={() => {
                                        this.addData();
                                    }}>
                                Submit Info
                            </button>
                        </Col>
                    </Row>
                    {this.state.alertModal ?
                    <Row>
                        <Col xs={{span:10, offset:1}}  md={{ span: 6, offset: 3 }}>
                            <Alert onClose={() => this.setState({alertModal:false})} 
                                variant={this.state.alertType} 
                                transition={true} 
                                dismissible={this.state.dismissible}
                                style={{marginTop: '5%'}}
                                >
                                <p style={{textAlign:'center'}}>{this.state.alertMessage}</p>
                            </Alert>
                        </Col>
                    </Row>
                    : null}
                    <div className="page-text" style={{paddingBottom: '10%', paddingTop:'5%'}}>
                        <br></br>
                        Homepage content goes here
                    </div>
                </Container>
            </div>
        );
    }
}

export default Homepage;