import React from 'react';
import ReactPlayer from 'react-player';
import '../styles/videoItem.css';

export default class VideoItem extends React.Component {

    constructor(props) {
        super(props);
    }

    render() {
        let url = "www.youtube.com/embed/";
        url += this.props.vidId;
        let factor = 0.3;

        return (
            <div>
                <div className='title'>{this.props.title}</div>
                <div className="large-container">
                    <ReactPlayer className="player" url={url} controls={true} width='640px' height='360px' />
                </div>
                <div className="medium-container">
                    <ReactPlayer className="player" url={url} controls={true} width='370px' height='200px' />
                </div>
                <div className="tiny-container">
                    <ReactPlayer className="player" url={url} controls={true} width='100%' />
                </div>
            </div>
        )
    }
};
