import React from 'react';
import VideoItem from './VideoItem';

export default class VideoList extends React.Component {

    constructor(props) {
        super(props);
    }

    render() { 
        let videos = this.props.videoObjects;
        let renderedVideos;
        if (videos.length > 0) {
            renderedVideos = videos.map((vid) => {
                return <VideoItem key={vid[0]} vidId={vid[0]} title={vid[1]} />
            });
        } else renderedVideos = <span style={{marginLeft: '5%', marginRight: '5%'}}>We don't have any videos up yet!</span>;
        return (    
            <div className='page-text'>{renderedVideos}</div>
        )
    }
}
