import React from 'react';
import axios from 'axios';
import VideoList from './VideoList';
import '../styles/videos.css';

class Videos extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            KEY: 'AIzaSyDSFgkpTHZLlKyGhlmPI77aDV3cxQmwZEg',
            //channelId: "UCm4OtG-eGdl0jqcvjf29cGQ", //THFJ youtube channel id
            channelId: "UCAMQyiQcTtDmIvbUEiwz6vg", //JQAS old youtube channel id, get new one
            uploadsId: null,
            videos: [],
            showVids: false
        }
    }

    componentDidMount() {
        this.getUploadsId();
    }
    
    getUploadsId = async () => {
        return new Promise((resolve, reject) => {
            axios.get('https://www.googleapis.com/youtube/v3/channels', {
                params: {
                    part:'contentDetails',
                    id: this.state.channelId,
                    key: this.state.KEY
                }
            }).then((res) => {
                this.setState({ uploadsId: res.data.items[0].contentDetails.relatedPlaylists.uploads});
            }).then((res) => {
                this.getVideosFromChannel();
                resolve();
            })
            .catch((error) => {
                console.log(`Error occurred retrieving youtube uploads id: ${error}`);
                reject();
            });
        });
    }

    getVideosFromChannel = async () => {
        let videos = [];
        let token = null;
        let response;
        let maxRes=50;
        let loop = true;
        while (loop) {
            response = await axios.get('https://www.googleapis.com/youtube/v3/playlistItems', {
                params: {
                    part: 'snippet',
                    playlistId: this.state.uploadsId,
                    key: this.state.KEY,
                    maxResults: maxRes,
                    pageToken: token
                }
            });

            if (response.data.items.length > 0) {
                this.setState({showVids: true});
            }

            //videos += response.data.items;
            for (let i = 0; i < maxRes; i++) {
                videos.push(response.data.items[i]);
            }
            token = response.data.nextPageToken;
            if (!token) {
                loop = false;
            }
        }
        
        //remove excess null values and send to getSpecificVideoIds
        let items = [];
        videos.forEach((i) => i == null ? null : items.push(i));
        this.getSpecificVideoIds(items);
    }

    getSpecificVideoIds = async (items) => {
        let videoIds = [];
        for (let i = 0; i < items.length; i++) {
            let vid = [];
            vid.push(items[i].snippet.resourceId.videoId);
            vid.push(items[i].snippet.title);
            videoIds.push(vid);
        }
        this.setState({ videos: videoIds });
    }

    
    render() {
        let h = Math.max(document.documentElement.clientHeight || 0, window.innerHeight || 0);
        h = this.state.videos.length == 2 || this.state.videos.length == 1 ? h/2 : this.state.videos.length == 0 ? h/1.75 : h;
        
        return (
            <div className="Videos">
                <div  class="header-text" style={{marginTop: '5%', marginBottom: '5%'}}>
                    <b><p style={{color: '#EBC13A'}}> JQAS Youtube Videos</p></b>
                </div>
                <div>
                    {this.state.videos.length > 0 ?
                        <p style={{marginLeft: '5%', marginRight: '5%'}} className="page-text">These are the <span style={{color: '#EBC13A'}}>JQAS at Berkeley</span> club videos</p>
                        : null
                    }
                    <div style={{marginLeft: '5%', marginRight: '5%'}} className="page-text"><a href="https://www.youtube.com/channel/UCAMQyiQcTtDmIvbUEiwz6vg/videos" className="youtube-link" target="_blank"> Click here</a> to go to our Youtube channel</div>
                    <br></br>
                </div>
                {this.state.videos.length > 2 ?
                    <div>
                        <VideoList videoObjects={this.state.videos} />
                    </div>
                    :
                    <div style={{height:h}}><VideoList videoObjects={this.state.videos} /></div>
                }
            </div>
        );
    }
}
export default Videos;